<!--
 * @Autor: linxu
 * @Date: 2022-01-21 15:05:20
 * @LastEditors: linxu
 * @LastEditTime: 2022-07-05 10:07:58
 * @Description: 机组云通讯
-->
<template>
  <div>
    <BMainTopComponent />
    <div class="crew-cloud-main">
      <!-- 筛选 -->
      <div class="search-box">
        <div class="search-item">
          <span>筛选:</span>
          <el-input v-model="input1" placeholder="机组名称"></el-input>
        </div>
        <div class="search-item">
          <el-input v-model="input2" placeholder="云猫标识"></el-input>
        </div>
        <el-button type="primary" icon="el-icon-search">搜索</el-button>
        <div class="right-box">
          <el-button class="add-btn" type="primary" icon="el-icon-plus" @click="handleAddData"
            >新增</el-button
          >
        </div>
      </div>
      <!-- 表格 -->
      <TableList
        :options="options"
        :columns="columns"
        :dataSource="dataSource"
        :operates="operates(this)"
        :dataTotal="dataTotal"
        :pagination="paginationCfg"
        @handleChangePage="handleChangePage"
        @handleChangeSize="handleChangeSize"
      />

      <!-- 新增/编辑 弹窗 -->
      <el-dialog
        :title="dialogType === 0 ? '新增' : '编辑'"
        class="add-edit-form-dialog"
        :visible.sync="addDialogVisible"
        :close-on-click-modal="false"
        :close-on-press-escape="false"
        @close="closeForm('ruleForm')"
      >
        <el-form class="add-edit-form" ref="ruleForm" :model="form" :rules="rules">
          <el-form-item label="产品类型" prop="value1" :label-width="formLabelWidth">
            <el-select v-model="form.value1" placeholder="请选择产品类型">
              <el-option label="控制器" value="1"></el-option>
              <el-option label="电子调速器" value="2"></el-option>
            </el-select>
          </el-form-item>
          <div class="form-flex-box">
            <el-form-item label="控制器品牌" prop="value3" :label-width="formLabelWidth">
              <el-select v-model="form.value3" placeholder="请选择控制器品牌">
                <el-option label="孚创" value="1"></el-option>
                <el-option label="孚创控制器" value="2"></el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="通讯协议" prop="value4" :label-width="formLabelWidth">
              <el-select v-model="form.value4" placeholder="请选择通讯协议">
                <el-option label="通讯协议1" value="1"></el-option>
                <el-option label="通讯协议2" value="2"></el-option>
              </el-select>
            </el-form-item>
          </div>
          <el-form-item label="机组名称" prop="DisplayName" :label-width="formLabelWidth">
            <el-input
              v-model="form.DisplayName"
              placeholder="请输入发电机组名称"
              autocomplete="off"
            ></el-input>
          </el-form-item>
          <el-form-item
            label="云猫标识"
            prop="CollectorToken"
            placeholder="请输入发电机组云猫标识"
            :label-width="formLabelWidth"
          >
            <el-input v-model="form.CollectorToken" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="型号" prop="CollectorType" :label-width="formLabelWidth">
            <el-select v-model="form.CollectorType" placeholder="请选择型号">
              <el-option label="型号1" value="1"></el-option>
              <el-option label="型号2" value="2"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="智慧曲线协议" prop="value2" :label-width="formLabelWidth">
            <el-select v-model="form.value2" placeholder="请选择">
              <el-option label="智慧曲线协议1" value="1"></el-option>
              <el-option label="智慧曲线协议2" value="2"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="维保消息" prop="radio1" :label-width="formLabelWidth">
            <el-radio-group v-model="form.radio1">
              <el-radio :label="1">是</el-radio>
              <el-radio :label="2">否</el-radio>
            </el-radio-group>
            <el-select v-model="form.value5" v-if="form.radio1 === 1" placeholder="请选择">
              <el-option label="维保消息协议1" value="1"></el-option>
              <el-option label="维保消息协议2" value="2"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="地区选择" prop="crewPositionValue" :label-width="formLabelWidth">
            <position-cascader
              :defaultPosition="crewPositionValue"
              @positionChange="positionChange"
            />
          </el-form-item>
          <el-form-item label="安装地点" prop="InstallationSite" :label-width="formLabelWidth">
            <el-input
              v-model="form.InstallationSite"
              placeholder="请输入发电机组安装地点"
              autocomplete="off"
            ></el-input>
          </el-form-item>
          <div class="form-flex-box">
            <el-form-item label="经度" prop="Longitude" :label-width="formLabelWidth">
              <el-input
                v-model="form.Longitude"
                oninput="value=value.replace(/[^0-9.]/g,'')"
                placeholder="请输入"
                autocomplete="off"
              ></el-input>
            </el-form-item>
            <el-form-item label="纬度" prop="Latitude" :label-width="formLabelWidth">
              <el-input
                v-model="form.Latitude"
                oninput="value=value.replace(/[^0-9.]/g,'')"
                placeholder="请输入"
                autocomplete="off"
              ></el-input>
            </el-form-item>
          </div>
          <el-form-item label="调参协议" prop="ProtocolId" :label-width="formLabelWidth">
            <el-select v-model="form.ProtocolId" placeholder="请选择">
              <el-option label="调参协议1" value="1"></el-option>
              <el-option label="调参协议2" value="2"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="物联网卡号" prop="SimNo" :label-width="formLabelWidth">
            <el-input
              v-model="form.SimNo"
              placeholder="请输入物联网卡号"
              autocomplete="off"
            ></el-input>
          </el-form-item>
          <el-form-item
            label="流量套餐(MB)"
            prop="MealValues"
            placeholder="请输入流量套餐"
            :label-width="formLabelWidth"
          >
            <el-input v-model="form.MealValues" autocomplete="off"></el-input>
            <span class="unit">MB</span>
          </el-form-item>
          <div class="form-flex-box">
            <el-form-item label="合闸分闸" prop="value6" :label-width="formLabelWidth">
              <el-radio-group v-model="form.value6">
                <el-radio :label="1">单按钮</el-radio>
                <el-radio :label="2">双按钮</el-radio>
              </el-radio-group>
            </el-form-item>
            <el-form-item label="有人值守" prop="value8" :label-width="formLabelWidth">
              <el-radio-group v-model="form.value8">
                <el-radio :label="1">是</el-radio>
                <el-radio :label="2">否</el-radio>
              </el-radio-group>
            </el-form-item>
          </div>
          <div class="form-flex-box">
            <el-form-item label="曲线" prop="value7" :label-width="formLabelWidth">
              <el-radio-group v-model="form.value7">
                <el-radio :label="1">是</el-radio>
                <el-radio :label="2">否</el-radio>
              </el-radio-group>
            </el-form-item>
            <el-form-item label="是否可以控制" prop="radio5" :label-width="formLabelWidth">
              <el-radio-group v-model="form.radio5">
                <el-radio :label="1">是</el-radio>
                <el-radio :label="2">否</el-radio>
              </el-radio-group>
            </el-form-item>
          </div>
          <div class="form-flex-box">
            <el-form-item label="远程调参" prop="radio6" :label-width="formLabelWidth">
              <el-radio-group v-model="form.radio6">
                <el-radio :label="1">是</el-radio>
                <el-radio :label="2">否</el-radio>
              </el-radio-group>
            </el-form-item>
            <el-form-item label="云消息" prop="radio7" :label-width="formLabelWidth">
              <el-radio-group v-model="form.radio7">
                <el-radio :label="1">是</el-radio>
                <el-radio :label="2">否</el-radio>
              </el-radio-group>
            </el-form-item>
          </div>
          <el-form-item label="机组解锁机" prop="radio8" :label-width="formLabelWidth">
            <el-radio-group v-model="form.radio8">
              <el-radio :label="1">是</el-radio>
              <el-radio :label="2">否</el-radio>
            </el-radio-group>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="closeForm('ruleForm')">取 消</el-button>
          <el-button type="primary" @click="submitForm('ruleForm')">{{
            dialogType === 0 ? '提 交' : '保 存'
          }}</el-button>
        </div>
      </el-dialog>

      <!-- 详情弹窗 -->
      <el-dialog
        title="详情"
        class="show-details-dialog"
        v-if="showDialogVisible"
        :visible.sync="showDialogVisible"
        :close-on-click-modal="false"
        :close-on-press-escape="false"
        @close="showDialogVisible = false"
      >
        <el-descriptions title="" :column="1" :colon="false">
          <el-descriptions-item label="机组名称">COMM012201010002</el-descriptions-item>
          <el-descriptions-item label="云猫标识">COMM012201010002</el-descriptions-item>
          <el-descriptions-item label="通讯协议状态">{{
            ['已完成', '下发中', '待下发'][0]
          }}</el-descriptions-item>
          <el-descriptions-item label="上次信号">
            <el-image style="width: 20px; height: 20px" :src="url"></el-image>
          </el-descriptions-item>
          <el-descriptions-item label="固件版本">V1.0.0</el-descriptions-item>
          <el-descriptions-item label="通讯说明">通讯正常</el-descriptions-item>
        </el-descriptions>
        <div slot="footer" class="dialog-footer">
          <el-button @click="showDialogVisible = false">取 消</el-button>
        </div>
      </el-dialog>
    </div>
  </div>
</template>

<script>
import { paginationConfigMixin } from '@/mixins/paginationConfigMixin'
import PositionCascader from '@/components/PositionCascader'
import moment from 'moment'
// 表格头设置
const columns = [
  {
    label: '机组名称',
    prop: 'DisplayName',
    minWidth: '12%',
    formatter: (row, column, index) => {
      // return arr[row.type]
    }
  },
  {
    label: '云猫标识',
    prop: 'CollectorToken',
    minWidth: '12%'
  },
  {
    label: '控制器品牌',
    prop: 'value3',
    minWidth: '12%'
  },
  {
    label: '通讯协议',
    prop: 'value4',
    minWidth: '10%'
  },
  {
    label: '调参协议',
    prop: 'ProtocolId',
    minWidth: '10%'
  },
  {
    label: '合闸分闸',
    prop: 'value6',
    minWidth: '8%'
  },
  {
    label: '曲线',
    prop: 'value7',
    minWidth: '5%'
  },
  {
    label: '有人值守',
    prop: 'value8',
    minWidth: '8%'
  },
  {
    label: '创建时间',
    prop: 'creatTime',
    minWidth: '15%'
  }
]
// 表格操作按钮
const operates = (that) => [
  {
    label: '编辑',
    icon: 'el-icon-edit-outline',
    method: (row) => {
      that.handleEdit(row)
    }
  },
  {
    label: '详情',
    icon: 'el-icon-info',
    method: (row) => {
      that.handleShow(row)
    }
  },
  {
    label: '删除',
    icon: 'el-icon-delete',
    method: (row) => {
      that.handleDelete(row)
    },
    setStyle: (row) => {
      return 'color: red;'
    }
  }
]
// 服务器请求返回值
const creatArray = function (val) {
  let arr = []
  for (let i = 1; i <= val; i++) {
    arr.push(i)
  }
  return arr
}
const dataArr = creatArray(12)
const dataSourceFun = () => {
  return dataArr.map((item, index) => {
    return {
      key: index,
      creatTime: moment(new Date()).format('yyyy-MM-DD HH:mm:ss')
    }
  })
}
export default {
  name: 'CrewCloud',
  mixins: [paginationConfigMixin],
  components: {
    PositionCascader
  },
  data() {
    return {
      input1: '',
      input2: '',
      // 表格
      columns,
      operates,
      dataSource: [],
      options: {
        index: true,
        labelIndex: '序号',
        props: {
          width: 248
        }
      },
      data: [],

      // 弹窗
      addDialogVisible: false,
      dialogType: 0,
      crewPositionValue: '',
      form: {
        DisplayName: '',
        CollectorToken: '',
        value1: '',
        value2: '',
        value3: '',
        value4: '',
        ProtocolId: '',
        value5: '',
        value6: 1,
        value7: 2,
        value8: 2,
        crewPosition: '', // 地区选择
        InstallationSite: '', // 安装地点
        Longitude: '',
        Latitude: '',
        SimNo: '', // 物联网卡号
        MealValues: '', // 流量套餐(MB)
        radio1: 2,
        radio5: 2,
        radio6: 2,
        radio7: 2,
        radio8: 2,
        creatTime: ''
      },
      formLabelWidth: '100px',
      rules: {
        DisplayName: [
          { required: true, message: '请输入发电机组名称', trigger: 'blur' }
          // { min: 3, max: 5, message: '长度在 3 到 5 个字符', trigger: 'blur' }
        ],
        CollectorToken: [{ required: true, message: '请输入发电机组云猫标识', trigger: 'blur' }],
        value3: [{ required: true, message: '请选择控制器品牌', trigger: 'change' }],
        value4: [{ required: true, message: '请选择通讯协议', trigger: 'change' }],
        CollectorType: [{ required: true, message: '请选择型号', trigger: 'change' }],
        ProtocolId: [{ required: true, message: '请选择调参协议', trigger: 'change' }],
        Longitude: [{ required: true, message: '请输入经度', trigger: 'blur' }],
        Latitude: [{ required: true, message: '请输入纬度', trigger: 'blur' }]
      },

      showDialogVisible: false,
      url: require('@/assets/img/crewmanage/ethernet_1.png')
    }
  },
  mounted() {
    this.data = dataSourceFun()
    this.dataTotal = this.data.length
    this.queryTableData()
  },
  methods: {
    queryTableData() {
      const arr = this.data.slice(
        (this.paginationCfg.currentPage - 1) * this.paginationCfg.pageSize,
        this.paginationCfg.currentPage * this.paginationCfg.pageSize
      )
      this.$set(this, 'dataSource', arr)
    },
    handleAddData() {
      this.dialogType = 0
      this.crewPositionValue = ''
      this.addDialogVisible = true
    },
    handleEdit(row) {
      console.log('edit', row, this.crewPositionValue)
      this.dialogType = 1
      this.$nextTick(() => {
        this.form = row
        this.crewPositionValue = '河北省辛集市'
      })
      this.addDialogVisible = true
    },
    handleShow(row) {
      console.log(row)
      this.showDialogVisible = true
    },
    handleDelete(row) {
      console.log(row)
      this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .then(() => {
          this.$message({
            type: 'success',
            message: '删除成功!'
          })
        })
        .catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          })
        })
    },
    // 地区选择切换
    positionChange(data) {
      console.log(data)
      this.form.crewPosition = data.crewPosition
    },

    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          alert('submit!')
          this.addDialogVisible = false
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },
    closeForm(formName) {
      this.resetForm(formName)
      this.crewPositionValue = null
      this.addDialogVisible = false
    },
    resetForm(formName) {
      this.$refs[formName].resetFields()
    }
  }
}
</script>

<style lang="less" scoped>
.crew-cloud-main {
  .search-box {
    justify-content: flex-start;
    background-color: #fff;
    .search-item {
      width: 20%;
    }
    .right-box {
      flex: 1;
      text-align: right;
    }
  }
}

/deep/.add-edit-form-dialog {
  .el-dialog {
    width: 640px;
    margin-top: 6vh !important;
  }
  .el-dialog__body {
    padding: 0;
    .add-edit-form {
      padding: 16px 24px;
      .el-form-item {
        // margin-bottom: 0;
        display: flex;
        .el-form-item__content {
          flex: 1;
          margin-left: 0 !important;
          display: flex;
          align-items: center;
          > div {
            flex: 1;
          }
          span.unit {
            width: 40px;
            text-align: center;
          }
        }
      }
      .form-flex-box {
        display: flex;
        .el-form-item {
          width: 50%;
        }
      }
    }
    .dialog-footer {
      border-top: 1px solid #e4e4e4;
      text-align: right;
      padding: 15px 0;
    }
  }
}

.show-details-dialog {
  /deep/.el-dialog {
    width: 600px;
  }
  /deep/.el-descriptions {
    .el-descriptions-item__label {
      width: 33.3%;
      text-align: right;
      color: #414141;
      font-weight: bold;
      margin-right: 16px;
    }
  }
}
</style>



